import MD5 from 'md5'

export const _isMobile = () => {
  let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
  return !!flag
}

/**
 * 模拟 sort 排序 (解决 Safari 下 sort 的 BUG)
 * @param  {Array}   array 需要排序的数组
 * @param  {Function} fn   排序函数
 * @return {Array}         返回排序后的数组
 */
 const _sort = (array, fn) => {
  for (var i = 0; i < array.length - 1; i++) {
    var isSorted = true
    for (var j = 0; j < array.length - 1 - i; j++) {
      if (fn(array[j], array[j + 1]) > 0) {
        var temp = array[j]
        array[j] = array[j + 1]
        array[j + 1] = temp
        isSorted = false
      }
    }
    if (isSorted) {
      return false
    }
  }
}

const paramSort = res => {
  var str = []
  // 将对象转成数组
  for (var i in res) {
    str.push([i, res[i]])
  }
  // 对数组进行排序
  _sort(str, function (a, b) {
    return a[0] > b[0]
  })
  return str
}

// 加密
export const signGenerate = res => {
  var objs = paramSort(res)
  var strs = ''
  for (let i = 0; i < objs.length; i++) {
    strs +=
      objs[i][0] +
      '' +
      (objs[i][1] === null || objs[i][1] === undefined ? '' : objs[i][1])
  }
  const _appSecret = import.meta.env.VITE_APP_API_APPSECRET
  strs = MD5(_appSecret + strs + _appSecret).toLowerCase()
  return strs
}