import { createApp } from 'vue'
import Vconsole from 'vconsole'
import App from './App.vue'
import routes from './utils/router'
import api from './utils/api'
import './index.css'
import './utils/reset.less'

let vConsole = import.meta.env.VITE_APP_ENV !== 'production' ? new Vconsole() : null
const app = createApp(App)
app.use(routes)
console.log('环境变量:--->', import.meta.env.VITE_APP_ENV)
import.meta.env.VITE_APP_ENV !== 'production' ? app.use(vConsole) : null
app.config.globalProperties.$apis = api
window.vm = app.mount('#app')
