import qs from 'qs'
import { signGenerate } from '../../utils/index'
function _handleUrlStatus (url) {
  const _arr = ['carbon/uploadFile']
  return _arr.includes(url)
}
/**
 * 拦截器发起请求
 *
 * @export
 * @param {*} res
 * @returns
 */

// Bridge
export const requestSuccess = res => {
  const filterParams = data => {
    if (data) {
      Object.keys(data).forEach(key => {
        if (data[key] === null || data[key] === undefined) {
          data[key] = ''
        }
      })
    }
  }
  let file = null
  if (_handleUrlStatus(res.url)) {
    // 处理图片上传
    file = new FormData()
    file.append('file', res.data.file)
    delete res.data.file
  }
  if (res.method === 'post') {
    // 兼容app_key 从 app 端获取情况
    const data = JSON.parse(JSON.stringify(res.data || {}))
    // data.app_terminal = import.meta.env.VITE_APP_TERMINAL
    data.app_key = import.meta.env.VITE_APP_API_APPKEY
    // 当前时间戳
    data.timestamp = new Date().getTime()
    data.token = ''
      // (Window.appConfig && Window.appConfig.token) ||
      // (global.$storage.get('token') ? global.$storage.get('token') : '')
    data.sign = signGenerate(data)
    if (_handleUrlStatus(res.url)) {
      // 处理车辆认证接口
      Object.keys(data).forEach(key => {
        file.append(key, data[key])
      })
      filterParams(data)
      res.data = file
      return res
    }
    filterParams(data)
    res.data = qs.stringify(data)
  }
  // CON.CONSOLE_REQUEST_ENABLE &&
  //   console.info('requestInterceptor', `url:[${res.method}] ${res.url}`, res)
  return res
}

/**
 * 拦截器发起请求失败
 *
 * @export
 * @param {*} requestError
 * @returns
 */
export const requestFail = requestError => {
  // 自定义发送请求失败逻辑，断网，请求发送监控等

  return Promise.reject(requestError)
}

/**
 * 拦截器响应请求成功
 *
 * @export
 * @param {*} responseObj
 * @returns
 */
export const responseSuccess = responseObj => {
  return responseObj.data
}

/**
 * 拦截器响应请求失败
 *
 * @export
 * @param {*} responseError
 * @returns
 */
export const responseFail = responseError => {
  // 响应失败，可根据 responseError.message 和 responseError.response.status 来做监控处理
  return Promise.reject(responseError)
}
